blockquote{font-family:Georgia,serif;position:relative;margin:.5em;padding:.5em 2em .5em 3em}blockquote:before{font-family:Georgia,serif;position:absolute;font-size:6em;line-height:1;top:0;left:0;content:"“"}blockquote:after{font-family:Georgia,serif;position:absolute;float:right;font-size:6em;line-height:1;right:0;bottom:-0.5em;content:"”"}