@font-face {
  font-family: "Nippo-Regular";
  src: local("Nippo-Regular"), url(./Nippo-Regular.otf) format("opentype");
}
html * {
  font-family: Nippo-Regular;
}

.hidden {
  display: none;
}

.App {
  text-align: center;
  max-height: 100vh;
  overflow: hidden;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#draggable {
  position: absolute;
  pointer-events: none;
  height: 100%;
  width: 100%;
}
